.products-list {
  display: flex;
  width: 100%;
  flex-wrap: wrap;
  margin-top: 5px; }

.product-card {
  border: 1px solid #eee;
  flex: 0 0 100%;
  padding: 5px; }
  @media (min-width: 480px) {
    .product-card {
      flex: 0 0 50%; } }
  @media (min-width: 700px) {
    .product-card {
      flex: 0 0 33.3333%; } }
  @media (min-width: 1024px) {
    .product-card {
      flex: 0 0 25%; } }
  .product-card__badge-container {
    min-height: 30px;
    margin: 5px 0; }
  .product-card__name {
    font-weight: normal;
    line-height: 26px;
    padding-right: 10px; }
  .product-card__price {
    font-size: 22px; }
