* {
  box-sizing: border-box; }

body {
  line-height: 1; }

ol,
ul {
  list-style: none; }

h1,
h2,
h3,
h4 {
  margin: 0;
  padding: 0; }

body {
  font-family: Arial, Helvetica, sans-serif; }

.flex-container {
  display: flex;
  justify-content: space-between; }

.align-center {
  margin: 0 auto; }

.text-center {
  text-align: center; }

.message {
  padding: 7px 15px;
  width: 100%;
  display: flex; }
  .message--error {
    background-color: lightcoral;
    color: #313131; }

.responsive-container {
  width: 100%;
  margin: 0 auto; }
  @media (min-width: 768px) {
    .responsive-container {
      width: 700px; } }
  @media (min-width: 1024px) {
    .responsive-container {
      width: 1000px; } }

.resp-img {
  width: 100%; }

.badge {
  padding: 10px 20px;
  display: inline-block;
  font-size: 12px; }
  .badge__sale {
    background-color: red;
    color: white; }
  .badge__exclusive {
    background-color: green;
    color: white; }
